* {
  margin: 0;
  padding: 0;
  list-style: none;
  text-decoration: none;
}

.app {
  
}

.portal {
  background-color:  rgb(26,28,31);
  background-size: cover;
  background-repeat: no-repeat;
  height: 100vh;
  
}

.overlay{
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;

  backdrop-filter: blur(3px);

}

.topBuffer {
  height:20vh;
  min-height:10vh;
}

.appBox {
  width: 30vw;
  height:auto;
  margin: auto;
  background-color: rgb(20,19,24);
  border:1px solid rgb(53,55,59);
  box-shadow: rgba(0, 0, 0, 0.1) 0px 19px 38px, rgba(0, 0, 0, 0.22) 0px 15px 12px;
  padding-bottom:1vh;
  border-radius: 1vh;
  
}

.appBox:hover {
  box-shadow: rgba(0, 0, 0, 0.2) 0px 19px 38px, rgba(0, 0, 0, 0.22) 0px 15px 12px;
}


.appBox-topBar {
padding:2vw;
font-size:8pt;
color:rgb(153,155,159);
